<template lang="ko">
<v-container>
    <!-- Article list -->
    <v-card>
      <v-card-text>
        <v-data-table-server
            v-model:items-per-page="itemsPerPage"
            :headers="headers"
            :items-length="totalItems"
            :items="serverItems"
            :loading="loading"
            class="elevation-1"
            item-title="name"
            item-value="name"
            @update:options="loadItems"
        ></v-data-table-server>
      </v-card-text>
      <v-card-actions>
        <v-btn color="primary" text @click="navigateToWritePage">Write</v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    itemsPerPage: 5,
    headers: [
      { title: "카테고리", key: "category", align: "end" },
      { title: "제목", key: "title", align: "end" },
      { title: "작성자", key: "writer", align: "end" },
      { title: "조회수", key: "viewCount", align: "end" },
      { title: "등록일시", key: "createdDate", align: "end" },
      { title: "수정일시", key: "modefiedDate", align: "end" },
    ],
    serverItems: [
      {
        category: "테스트",
        title: "제목테스트",
        writer: "작성자테스트",
        viewCount: "1",
        createdDate: "2020.01.01",
        modefiedDate: "2020.01.01",
      },

      {
        category: "테스트",
        title: "제목테스트",
        writer: "작성자테스트",
        viewCount: "1",
        createdDate: "2020.01.01",
        modefiedDate: "2020.01.01",
      },

      {
        category: "테스트",
        title: "제목테스트",
        writer: "작성자테스트",
        viewCount: "1",
        createdDate: "2020.01.01",
        modefiedDate: "2020.01.01",
      },

      {
        category: "테스트",
        title: "제목테스트",
        writer: "작성자테스트",
        viewCount: "1",
        createdDate: "2020.01.01",
        modefiedDate: "2020.01.01",
      },

      {
        category: "테스트",
        title: "제목테스트",
        writer: "작성자테스트",
        viewCount: "1",
        createdDate: "2020.01.01",
        modefiedDate: "2020.01.01",
      },

      {
        category: "테스트",
        title: "제목테스트",
        writer: "작성자테스트",
        viewCount: "1",
        createdDate: "2020.01.01",
        modefiedDate: "2020.01.01",
      },
    ],
    loading: true,
    totalItems: 0,
  }),
  beforeMount() {
    this.loadItems({ page: 1, itemsPerPage: 5, sortBy: [] });
  },
  methods: {
    async loadItems({ page, itemsPerPage, sortBy }) {
      try {
        this.loading = true;

        const response = await this.axios.get("/v1/articles", {
          params: {
            _page: page,
            _limit: itemsPerPage,
            _sort: sortBy[0] ? sortBy[0].value : null,
            _order: sortBy[0] && sortBy[0].sortDesc ? "desc" : "asc",
          },
        });

        this.serverItems = response.data;
        this.totalItems = Number(response.headers["x-total-count"]);
      } catch (error) {
        console.error(error);
      } finally {
        this.loading = false;
      }
    },
    navigateToWritePage() {
      this.$router.push({ name: "article-write" });
    },
  },
};
</script>
